import { Component, OnInit } from '@angular/core';
import { NgForm, FormBuilder, FormGroup, Validators } from '@angular/forms';
import { SettingsService } from '../../services/settings.service';

@Component({
  selector: 'settings-company',
  templateUrl: './settings-company.component.html',
  styleUrls: ['./settings-company.component.scss']
})
export class SettingsCompanyComponent implements OnInit {
  public industries: any[];
  public currencies: any[];
  public countries: any[];
  public timezones: any[];

  //Model
  public name : string;
  public companyType: number;
  public industryId: number;
  public currencyId: number;
  public countryId: number;
  public address1: string;
  public address2: string;
  public city: string;
  public postalCode: string;
  public email: string;
  public workPhone: string;
  public mobilePhone: string;
  public fax: string;
  public timezone: string;

  protected _fb: FormBuilder;
  companyForm: FormGroup;

  constructor(settingsService: SettingsService, fb: FormBuilder) {
    settingsService.getCompanyTypes().subscribe( (data: Array<Map<string, string>>) => {
      this.industries = data;
    });
    settingsService.getCurrencies().subscribe( (data: Array<Map<string, string>>) => {
      this.currencies = data;
    });
    settingsService.getCountries().subscribe( (data: Array<Map<string, string>>) => {
      this.countries = data;
    });
    settingsService.getTimezones().subscribe( (data: Array<Map<string, string>>) => {
      this.timezones = data;
    });

    this._fb = fb;

    this.createForm();
   }

   createForm() {
    //form control name should match with User class property name
    this.companyForm= this._fb.group({
      name: ['fffff', Validators.required ],
      type: '',
      industry: '',
      currency: '',
      country: ['', Validators.required],
      address1: '',
      address2: '',
      city: '',
      province: '',
      postalCode: '',
      email: ['', [Validators.required, Validators.email]],
      workPhone: '',
      mobilePhone: '',
      fax: '',
      timezone: '',
      website: ''
    });
  }

  ngOnInit() {}

  onSubmit() {
    console.log(this.companyForm.value);
  }
}
