import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class SettingsService {
  apiPath : string = 'https://localhost:8443/api/';

  constructor(private httpClient: HttpClient) { }

  public getCompanyTypes() {return this.httpClient.get(`${this.apiPath}industry`)};
  public getCurrencies() {return this.httpClient.get(`${this.apiPath}currency`)};
  public getCountries() {return this.httpClient.get(`${this.apiPath}country`)};
  public getTimezones() {return this.httpClient.get(`${this.apiPath}timezone`)};
}
